import { getResourceList } from "@/api/modules/web/resource";
import FilterHeader from "@/components/FilterHeader";
import { Card, message } from "antd";
import Table, { ColumnsType } from "antd/es/table";
import { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";

interface DataType {
    key: React.Key;
    name: string;
    entryType: string;
    startTime: string;
    duration: string;
    // initiatorType: string;
    // nextHopProtocol: string;
    // renderBlockingStatus: string;
    // workerStart: string;
    // redirectStart: string;
    // redirectEnd: string;
    // fetchStart: string;
    // domainLookupStart: string;
    // domainLookupEnd: string;
    // connectStart: string;
    // secureConnectionStart: string;
    // connectEnd: string;
    // requestStart: string;
    // responseStart: string;
    // responseEnd: string;
    // transferSize: string;
    // encodedBodySize: string;
    // decodedBodySize: string;
    // responseStatus: string;
    // serverTiming: string;
}

const ResourceList = () => {

    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [resourceTotal, setResourceTotal] = useState(0);
    const [resourceList, setResourceList] = useState<DataType[]>([]);
    const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);
    const [searchParams] = useSearchParams();
    const [messageApi, contextHolder] = message.useMessage();

    const columns: ColumnsType<DataType> = [
        {
            title: '静态资源路径',
            dataIndex: 'name',
            width: 100,
        },
        {
            title: 'entryType',
            dataIndex: 'entryType',
            width: 200,
        },
        {
            title: '资源请求时间',
            dataIndex: 'startTime',
            width: 200,
        },
        {
            title: '资源请求耗时',
            dataIndex: 'duration',
            width: 200,
        },
    ];

    const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
        setSelectedRowKeys(newSelectedRowKeys);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const initResourceList = async () => {
        const appId = searchParams.get('appId');
        if (!appId) {
            messageApi.open({
                type: 'error',
                content: 'appId不能为空!',
            });
            return;
        }
        const result = await getResourceList({ pageNum, pageSize, appId });
        if (result.data) {
            setResourceTotal(result.data.total);
            const data: DataType[] = [];
            result.data.data.forEach(item => {
                data.push({
                    key: item.resourceId,
                    name: item.name,
                    entryType: item.entryType,
                    startTime: item.startTime,
                    duration: item.duration
                })
            });
            setResourceList(data);
        }
    };

    useEffect(() => {
        initResourceList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        initResourceList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize])

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: resourceTotal
    }

    return (<div>
        <FilterHeader></FilterHeader>
        <Card style={{ marginTop: '16px' }} title="静态资源列表">{contextHolder}<Table style={{ marginTop: '12px' }} rowSelection={rowSelection} pagination={pagination} columns={columns} dataSource={resourceList} /></Card>
    </div>);
}

export default ResourceList;